@use '/src/scss';

.listcard-container {
  display: flex;
  border: 1px solid scss.$main-color;
  border-radius: 5px;
  padding: 0.6rem;
  height: 150px;
  align-items: stretch;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto auto;
}

.listcard-details {
  padding: 0 1rem;
  text-align: left;
  line-height: 1.6rem;
  flex: 3;
  width: 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: scss.$color-light;
  text-shadow: 2px 2px 2px rgb(0, 0, 0);
  font-size: 1.2rem;
  font-weight: bold;
  z-index: 2;

  div {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

.details-item {
  text-transform: capitalize;
}

.details-price-range {
  color: scss.$main-color;
}

.listcard-button-container {
  margin-left: 0.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-end;
  margin-left: auto;
  z-index: 1;
}

.card-button {
  height: 2rem;
  display: flex;
  justify-content: space-around;
  padding: 0.2rem;
  align-items: center;
  background-color: rgba(24, 24, 24, 0.7);
  border-radius: 25px;
  border: 1px solid rgba(200, 200, 200, 0.4);
  color: scss.$color-light;
  width: 5rem;
}

.delete-button {
  &:hover {
    color: #e00c0c;
  }
}

.edit-button {
  &:hover {
    color: #e0b90c;
  }
}
